<template lang="pug">
  .ring(:class="classObject")
    b-container(fluid)
      b-row
        b-col.ring__title-block(cols="12")
          h1 {{ title }}
      b-row
        b-col.ring__image-block(cols="12" md="7")
        b-col.ring__text-block.ring__text-block--1(cols="12" md="5")
          p(v-for="eachParagraph in paragraph1") {{ eachParagraph }}
      b-row
        b-col.ring__text-block.ring__text-block--2(
          cols="12" md="5" order="2" order-md="1"
        )
          p(v-for="eachParagraph in paragraph2") {{ eachParagraph }}
        b-col.ring__image-block(cols="12" md="7" order="1" order-md="2")
      b-row
        b-col.ring__ring-introdution(cols="12")
      b-row
        b-col.ring__text-block.ring__text-block--3(clos="12")
          p(v-for="eachParagraph in paragraph3") {{ eachParagraph }}
      b-row
        b-col.ring__back-button(cols="2" offset="1")
          decorated-button(
            @click="$router.push('/marriage-story-ring')"
            :color="'golden'"
            :buttonDescription="'BACK'")
</template>

<script>
import ringInfo from '@/utils/ringInfo';
import DecoratedButton from '@/components/DecoratedButton.vue';

export default {
  data() {
    return {
      lang: 'zhTW',
      // lang: 'en',
      id: this.$route.params.id,
      ringInfo,
    };
  },
  components: {
    DecoratedButton,
  },
  computed: {
    classObject() {
      return [this.id ? `ring--${this.id}` : ''];
    },
    specificRingInfo() {
      return ringInfo[this.lang][this.id]
        ? ringInfo[this.lang][this.id]
        : ringInfo[this.lang].distance;
    },
    title() {
      return this.specificRingInfo.title;
    },
    paragraph1() {
      return this.specificRingInfo.paragraph1;
    },
    paragraph2() {
      return this.specificRingInfo.paragraph2;
    },
    paragraph3() {
      return this.specificRingInfo.paragraph3;
    },
  },
};
</script>

<style lang="scss">
@import "../scss/_mediaQuery.scss";

.ring {
  h1 {
    text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.5);
    font-size: 40px;
    color: white;
    font-weight: 300;
    @include tablet-and-mobile-only {
      font-size: 25px;
    }
  }
  &__title-block {
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: no-repeat center center;
    background-size: cover;
  }
  &__image-block {
    width: 100%;
    background: no-repeat center center;
    background-size: 100%;
    @include tablet-landscape-and-up {
      height: 600px;
    }
    @include tablet-and-mobile-only {
      height: 275px;
    }
  }
  &__text-block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include tablet-landscape-and-up {
      // height: 600px;
    }
    @include tablet-and-mobile-only {
      padding: 30px 40px;
      text-align: left;
    }
    &--1 {
      p {
        width: 25vw;
        @include tablet-and-mobile-only {
          width: 80vw;
        }
      }
    }
    &--2 {
      p {
        width: 33.3vw;
        @include tablet-and-mobile-only {
          width: 80vw;
        }
      }
    }
    &--3 {
      margin-top: 50px;
      p {
        width: 83.3vw;
      }
    }
    p {
      text-align: left;
      font-size: 15px;
      line-height: 1.73;
      letter-spacing: 0.5px;
      color: #3e3a39;
    }
  }
  &__ring-introdution {
    width: 100%;
    background: no-repeat center center;
    background-size: cover;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-left: 0;
    padding-right: 0;
    @include tablet-landscape-and-up {
      height: 600px;
    }
    @include tablet-and-mobile-only {
      height: 325px;
    }
  }
  &__ring-description {
    width: 100%;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: rgba(0, 0, 0, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__back-button {
    margin-top: 60px;
    margin-bottom: 60px;
  }
  @mixin ring-series-background-group($ring-type) {
    .ring__title-block {
      background-image: url('~assets/marriageStoryRing/' + $ring-type + '/banner@2x.png');
      @include tablet-and-mobile-only {
        background-image: url('~assets/marriageStoryRing/' + $ring-type + '/banner-m@2x.png');
      }
    }
    .ring__image-block:nth-of-type(1) {
      background-image: url('~assets/marriageStoryRing/' + $ring-type + '/1@2x.png');
    }
    .ring__image-block:nth-of-type(2) {
      background-image: url('~assets/marriageStoryRing/' + $ring-type + '/2@2x.png');
    }
    .ring__ring-introdution {
      background-image: url('~assets/marriageStoryRing/' + $ring-type + '/3@2x.png');
      @include tablet-and-mobile-only {
        background-image: url('~assets/marriageStoryRing/' + $ring-type + '/3-m@2x.png');
        background-size: 100%;
      }
    }
  }
  &--distance {
    @include ring-series-background-group('distance');
  }
  &--marriageMemory {
    @include ring-series-background-group('marriageMemory');
  }
  &--meet {
    @include ring-series-background-group('meet');
  }
  &--modenFairyTale {
    @include ring-series-background-group('modenFairyTale');
  }
  &--nature {
    @include ring-series-background-group('nature');
  }
  &--ropeKnot {
    @include ring-series-background-group('ropeKnot');
  }
  &--sheetMusic {
    @include ring-series-background-group('sheetMusic');
  }
  &--snails {
    @include ring-series-background-group('snails');
  }
  &--starrySky {
    @include ring-series-background-group('starrySky');
  }
  &--wineAndBass {
    @include ring-series-background-group('wineAndBass');
  }
}
</style>
